.swipe-button {
  display: flex;
  justify-content: space-evenly;
  position: fixed;
  bottom: 2vh;
  width: 100%;
}

.swipe-button > * {
  margin: 0 10px;
}

.swipe-button .MuiIconButton-root {
  background-color: pink;
  border-radius: 50%;
  transition: background-color 0.2s;
}

.swipe-button .MuiIconButton-root:hover {
  background-color: yellow;
}

.swipe-button .MuiIconButton-colorPrimary {
  color: blue;
}
