.tinderCards__container {
  display: flex;
  justify-content: center;
  margin-top: 1vh;
}

.card {
  position: relative;
  background-color: white;
  width: 350px;
  padding: 30px;
  max-width: 45vw;
  height: 55vh;
  box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}

.swipe {
  position: absolute;
}

.card h3 {
  position: absolute;
  bottom: 0;
  margin: 10px;
  color: white;
}
